* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: white;
}
header{
  position: absolute;
  top: 25px;
  left: 10px;
  z-index: 1;    
}
header a{
  font-size: 1.2em;  
  text-decoration: none;  
}
.menu{
position: absolute;
top: 0;
width: 100vw;
height: 100vh;
/* background: red; */
}
.inspiration{
  position: absolute;
  bottom: 2px;
  right: 0;
}
.shaders-section{
  position: absolute;
  display: grid;
}
.shaders-section button{
  background: black;
  color: white;
  border: 0;
  margin: 0.1em;
  padding: 0.4em 0.8em;
  cursor: pointer;
}
.shader-overlay-container{
  position: absolute;  
  top:0px;
  width: 100vw;
  height: 100vh;
  background:#000000ea;
  color:white;
}
.shader-overlay{
  display: grid;  
  justify-content: center;  
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}
.close{
  position: absolute;  
  top: 25px;
  right: 3em;
  font-size: 1.2em;  
  cursor: pointer;
}
.gui{
  position: absolute;
  top: 0;
  right: 0;
}
.tagline{
  width: 100%;
  height: 100px;
  display: grid;
  align-items: center;
  justify-content: center;
  font-size: 3em;
  font-weight: bolder;
  background: black;
  color: white;
}
.card-wrapper{
  margin: 1em auto;
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1em;
}
.card{
  width: 100%;
  margin-top: 0.5em;
}
.card h5{
  margin: 0px 0px 0.2em 0px;   
}
.card img{
  width: 95%;
  border: 1px solid rgb(129, 129, 129);
}
.card-wrapper a{
  text-decoration: none;
  color: black;
  font-size: 1.3em; 
}